@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;600;700;900&display=swap');
@import 'header';
@import "acceuil";
@import "mediaQueries";
@import "footer";
@import "variable";

body {
    overflow-x: hidden;
}

.header__text {
    display: none;
}

.progressbar {
    height: 2em;
    width: 56em;
    margin-top: 1.5em;
    background-color: #fff;
    border-radius: 5px;
    border: 10px solid #000;
    margin-left: 10%;
}

.numerotation {
    display: flex;
    justify-content: space-between;

    p {
        font-size: 2em;
        font-weight: 700;
        color: $text_color;
    }
}

.relative {
    position: relative;
}

#file {
    margin-top: 1.5em;
    width: 55em;
    margin-left: 10%;
    position: absolute;
    top: 8px;
    right: -9.3%;

}

.main {
    &__inform {
        text-align: center;
        margin-bottom: 3%;

        &__img {
            width: 2.5em;
            height: 2.5em;

            &--active {
                border: 10px solid $btn_color;
                border-radius: 50%;

            }
        }

        a {
            color: white;
            padding: 0% 1%;
            background-color: $btn_color;
            border-radius: 5px;
            font-size: 2em;
            font-weight: 700;

        }
    }

    &__tags {
        display: flex;
        justify-content: space-between;
        width: 50%;
        margin: 3% auto;
    }

    &__tag {
        h3 {
            color: $btn_color;
            font-weight: 700;
        }
    }

    &__Preambule {
        width: 60%;
        margin: 2% auto;
        background-color: $bg_test;
        padding: 3% 2% 4%;
        border-radius: 5px;

        h3 {
            color: $btn_color;
            font-weight: 700;
            font-size: 2em;

        }

        p {
            margin: 2% 0;

        }
    }

    &__questionnaire {
        text-align: center;
    }
}




/////////////////js////////////////
.affiche {
    display: none;
}

form {
    border: 3px solid $bg_test;
    border-radius: 1em;
    padding: 2em 4em;
    max-width: 65%;
    margin: 3em auto;
}

.barNumber {
    border: 3px solid $bg_test;
    border-radius: 1em;
    margin-bottom: 1em;
}

.questions {
    p {
        font-weight: 700;
        font-size: 1.5em;
        color: $color2;
        height: 15vh;
        padding-top: 5%;
    }
}




.reponces {
    height: 24vh;
    display: flex;
    padding-top: 5%;
    justify-content: space-evenly;

    label {
        margin-left: 0.4em;
        font-size: 2em;
    }

    .inputText {
        margin-left: 25%;
    }
}


.inputs {
    display: flex;
    justify-content: space-between;

    #precedent,
    #suivant,
    #resultat {
        border: 1px dotted $text_color;
        border-radius: 5px;
        padding: 5px 10px;
        font-size: 1.5em;
        font-weight: 600;
        background-color: $btn_color;
    }

}

input[type="number"] {
    width: 75%;
    height: 40px;
    border: 2px solid #96c5dc;
    border-radius: 10px;
    text-align: center;
}

input[type="number"]:hover {
    box-shadow: 0 0 0 1px #96c5dc;
    color: #0f6894;
}

input[type="radio"]:after {
    width: 32px;
    height: 32px;
    border-radius: 15px;
    top: -16px;
    left: -10px;
    position: relative;
    background-color: white;
    content: "";
    display: inline-block;
    visibility: visible;
    border: 5px solid $btn_color;
}

input[type="radio"]:checked:after {
    width: 32px;
    height: 32px;
    border-radius: 15px;
    top: -16px;
    left: -10px;
    position: relative;
    background-color: $bg_color;
    content: "";
    display: inline-block;
    visibility: visible;
    border: 5px solid $btn_color;
}